import Fuse from 'fuse.js';

// 
interface ImageSearchData {
  id: string;
  file: string;
  title: string;
  category: string;
  keywords: string[];
  description: string;
  searchText?: string;
  pngFallback?: string;
}

interface SearchResult {
  item: ImageSearchData;
  score?: number;
}

// 
let imageSearchData: ImageSearchData[] = [];
let fuse: Fuse<ImageSearchData> | null = null;
let isDataLoaded = false;
let isLoading = false;

// Fuse
const fuseOptions: Fuse.IFuseOptions<ImageSearchData> = {
  keys: [
    { name: 'title', weight: 0.3 },
    { name: 'description', weight: 0.2 },
    { name: 'keywords', weight: 0.3 },
    { name: 'category', weight: 0.1 },
    { name: 'searchText', weight: 0.1 }
  ],
  threshold: 0.6,
  distance: 100,
  minMatchCharLength: 1,
  includeScore: true,
  ignoreLocation: true,
  useExtendedSearch: true
};

// 
async function loadImageSearchData(): Promise<void> {
  if (isLoading || isDataLoaded) return;

  isLoading = true;
  console.log(' ');

  try {
    // knowledge-baseJSON
    let response = await fetch('/knowledge-base/data/image_search_data.json?t=' + Date.now(), {
      cache: 'no-store',
      headers: {
        'pragma': 'no-cache',
        'cache-control': 'no-cache'
      }
    });

    // knowledge-baseAPI
    if (!response.ok) {
      console.log('knowledge-baseAPI');
      const initResponse = await fetch('/api/tech-support/init-image-search-data', {
        method: 'POST'
      });
      
      if (initResponse.ok) {
        console.log('API');
        // knowledge-base
        response = await fetch('/knowledge-base/data/image_search_data.json?t=' + Date.now(), {
          cache: 'no-store'
        });
      }
    }

    if (response.ok) {
      const data = await response.json();

      if (Array.isArray(data) && data.length > 0) {
        imageSearchData = data;

        // searchText
        imageSearchData = imageSearchData.map(item => ({
          ...item,
          searchText: [
            item.title,
            item.description,
            ...(item.keywords || []),
            item.category
          ].filter(Boolean).join(' ')
        }));

        // Fuse
        fuse = new Fuse(imageSearchData, fuseOptions);
        isDataLoaded = true;

        console.log(` : ${imageSearchData.length}`);
        console.log(' Fuse');

        // 
        const testResults = fuse.search('');
        console.log(` : ${testResults.length}`);

        return;
      }
    }

    throw new Error('');

  } catch (error) {
    console.error(' :', error);
    imageSearchData = [];
    fuse = null;
    isDataLoaded = false;
  } finally {
    isLoading = false;
  }
}

// 
export async function searchImages(query: string): Promise<ImageSearchData[]> {
  console.log(' :', query);

  // 
  if (!isDataLoaded && !isLoading) {
    await loadImageSearchData();
  }

  // 
  if (isLoading) {
    console.log(' ...');
    let attempts = 0;
    while (isLoading && attempts < 50) { // 5
      await new Promise(resolve => setTimeout(resolve, 100));
      attempts++;
    }
  }

  if (!fuse || !isDataLoaded || imageSearchData.length === 0) {
    console.log(' Fuse');
    console.log(':', { 
      fuseExists: !!fuse, 
      isDataLoaded, 
      dataCount: imageSearchData.length 
    });
    
    // 
    if (!isDataLoaded) {
      console.log(' ');
      await loadImageSearchData();
      
      // 
      if (fuse && imageSearchData.length > 0) {
        console.log(' ');
      } else {
        return [];
      }
    } else {
      return [];
    }
  }

  if (!query || query.trim().length === 0) {
    console.log(' ');
    return [];
  }

  try {
    console.log(` Fuse: "${query}"`);
    console.log(' :', imageSearchData.length);

    // Fuse
    const results = fuse.search(query);
    console.log(` Fuse: ${results.length}`);

    if (results.length > 0) {
      results.forEach((result, index) => {
        console.log(` ${index + 1}: ${result.item.title} (: ${result.score})`);
      });
    }

    // 
    const items = results.map(result => result.item).slice(0, 10);
    console.log(` : ${items.length}`);

    return items;

  } catch (error) {
    console.error(' :', error);
    return [];
  }
}

// 
export const reloadImageSearchData = async (): Promise<void> => {
  console.log(' ');
  isDataLoaded = false;
  isLoading = false;
  imageSearchData = [];
  fuse = null;
  await loadImageSearchData();
};

// 
let searchAbortController: AbortController | null = null;

// 
export const cancelSearch = (): void => {
  if (searchAbortController) {
    searchAbortController.abort();
    searchAbortController = null;
    console.log('');
    console.log('');
  }
};

// 
export const searchByText = async (query: string, isManualSearch: boolean = false): Promise<any[]> => {
  return await searchImages(query);
};

// 
export const initializeImageSearch = async (): Promise<void> => {
  if (!isDataLoaded && !isLoading) {
    await loadImageSearchData();
  }
};

// 
initializeImageSearch();