/**
 * Text-to-speech functionality using Web Speech API
 */

/**
 * Speak text using the browser's native speech synthesis
 * @param text The text to speak
 * @param options Voice options
 * @returns Promise that resolves when speech is finished or rejects if there's an error
 */
export const speakText = (
  text: string,
  options: {
    rate?: number;
    pitch?: number;
    volume?: number;
    lang?: string;
  } = {}
): Promise<void> => {
  return new Promise((resolve, reject) => {
    // SpeechSynthesis API
    if (!('speechSynthesis' in window)) {
      reject(new Error(''));
      return;
    }

    // SpeechSynthesisUtterance
    const utterance = new SpeechSynthesisUtterance(text);

    // 
    utterance.rate = options.rate || 1.0; //  (0.1-10)
    utterance.pitch = options.pitch || 1.0; //  (0-2)
    utterance.volume = options.volume || 1.0; //  (0-1)
    utterance.lang = options.lang || 'ja-JP'; // 

    // 
    utterance.onend = () => {
      resolve();
    };
    
    utterance.onerror = (event) => {
      reject(new Error(`: ${event.error}`));
    };

    // 
    window.speechSynthesis.cancel();

    // 
    window.speechSynthesis.speak(utterance);
  });
};

/**
 * 
 */
export const stopSpeaking = (): void => {
  if ('speechSynthesis' in window) {
    window.speechSynthesis.cancel();
  }
};

/**
 * 
 * @returns 
 */
export const getAvailableVoices = (): Promise<SpeechSynthesisVoice[]> => {
  return new Promise((resolve) => {
    if (!('speechSynthesis' in window)) {
      resolve([]);
      return;
    }

    // 
    let voices = window.speechSynthesis.getVoices();
    if (voices.length > 0) {
      resolve(voices);
      return;
    }

    // 
    window.speechSynthesis.onvoiceschanged = () => {
      voices = window.speechSynthesis.getVoices();
      resolve(voices);
    };
  });
};

/**
 * 
 * @param lang : 'ja-JP'
 * @returns null
 */
export const selectVoiceForLanguage = async (
  lang: string
): Promise<SpeechSynthesisVoice | null> => {
  const voices = await getAvailableVoices();
  
  // 
  const exactMatch = voices.find(
    (voice) => voice.lang.toLowerCase() === lang.toLowerCase()
  );
  if (exactMatch) return exactMatch;
  
  // : 'ja-JP'  'ja'
  const langPrefix = lang.split('-')[0].toLowerCase();
  const prefixMatch = voices.find(
    (voice) => voice.lang.toLowerCase().startsWith(langPrefix)
  );
  if (prefixMatch) return prefixMatch;
  
  // null
  return voices.length > 0 ? voices[0] : null;
};