// drizzle-orm
import { pgTable, text, timestamp, jsonb, integer, boolean } from 'drizzle-orm/pg-core';
import { sql } from 'drizzle-orm';

// 
// 
export const users = pgTable('users', {
  id: text('id').primaryKey().default(sql`gen_random_uuid()`),
  username: text('username').notNull().unique(),
  password: text('password').notNull(),
  display_name: text('display_name').notNull(),
  role: text('role').notNull().default('employee'),
  department: text('department'),
  description: text('description'),
  created_at: timestamp('created_at').defaultNow().notNull()
});


// 
// 
export const chats = pgTable('chats', {
  id: text('id').primaryKey().default(sql`gen_random_uuid()`), // UUID
  userId: text('user_id').notNull(), // ID
  title: text('title'), // 
  createdAt: timestamp('created_at').defaultNow().notNull() // 
});

// 
// 
export const messages = pgTable('messages', {
  id: text('id').primaryKey().default(sql`gen_random_uuid()`), // UUID
  chatId: text('chat_id').notNull(), // ID
  senderId: text('sender_id').notNull(), // ID
  content: text('content').notNull(), // 
  isAiResponse: boolean('is_ai_response').notNull().default(false), // AI
  createdAt: timestamp('created_at').defaultNow().notNull() // 
});

// 
// 
export const media = pgTable('media', {
  id: text('id').primaryKey().default(sql`gen_random_uuid()`), // UUID
  messageId: integer('message_id').notNull(), // ID
  type: text('type').notNull(), // 
  url: text('url').notNull(), // URL
  description: text('description'), // 
  createdAt: timestamp('created_at').defaultNow().notNull() // 
});

// 
// 
export const emergencyFlows = pgTable('emergency_flows', {
  id: text('id').primaryKey().default(sql`gen_random_uuid()`), // UUID
  title: text('title').notNull(), // 
  steps: jsonb('steps').notNull(), // JSON
  keyword: text('keyword').notNull(), // 
  createdAt: timestamp('created_at').defaultNow().notNull() // 
});

// 
// 
export const images = pgTable('images', {
  id: text('id').primaryKey().default(sql`gen_random_uuid()`), // UUID
  url: text('url').notNull(), // URL
  description: text('description').notNull(), // 
  embedding: jsonb('embedding').notNull(), // AI
  createdAt: timestamp('created_at').defaultNow().notNull() // 
});

// 
// 
export const documents = pgTable('documents', {
  id: text('id').primaryKey().default(sql`gen_random_uuid()`), // UUID
  title: text('title').notNull(), // 
  content: text('content').notNull(), // 
  userId: text('user_id').notNull(), // ID
  createdAt: timestamp('created_at').defaultNow().notNull() // 
});

// 
// 
export const keywords = pgTable('keywords', {
  id: text('id').primaryKey().default(sql`gen_random_uuid()`), // UUID
  documentId: text('document_id'), // ID
  word: text('word').notNull(), // 
  createdAt: timestamp('created_at').defaultNow().notNull() // 
});

// 
// 
export const chatExports = pgTable('chat_exports', {
  id: text('id').primaryKey().default(sql`gen_random_uuid()`), // UUID
  chatId: text('chat_id').notNull(), // ID
  userId: text('user_id').notNull(), // ID
  timestamp: timestamp('timestamp').defaultNow().notNull() // 
});

// Zod
import { z } from 'zod';

export const loginSchema = z.object({
  username: z.string(),
  password: z.string()
});

export const insertUserSchema = z.object({
  username: z.string(),
  password: z.string(),
  display_name: z.string(),
  role: z.string(),
  department: z.string().optional()
});

export const insertChatSchema = z.object({
  id: z.string().optional(),
  userId: z.string(),
  title: z.string().optional()
});

export const insertMessageSchema = z.object({
  chatId: z.string(),
  content: z.string(),
  isAiResponse: z.boolean().default(false),
  senderId: z.string().nullable(),
});

export type InsertMessage = z.infer<typeof insertMessageSchema>;

// ChatMessage
export interface ChatMessage {
  id: string;
  chatId: string;
  content: string;  // URL
  text: string;     // content
  isAiResponse: boolean;
  senderId: string | null;
  createdAt: Date;
  timestamp?: Date;
  role?: 'user' | 'assistant';
  media?: { type: string, url: string, thumbnail?: string }[];
}

// MessageChatMessage
export interface Message extends ChatMessage {}

export const insertMediaSchema = z.object({
  messageId: z.number(),
  type: z.string(),
  url: z.string(),
  description: z.string().optional()
});

export const insertDocumentSchema = z.object({
  title: z.string(),
  content: z.string(),
  userId: z.string()
});

export const insertKeywordSchema = z.object({
  documentId: z.string().optional(),
  word: z.string()
});

// Type exports for better TypeScript support
export type User = typeof users.$inferSelect;
export type InsertUser = typeof users.$inferInsert;

export type Chat = typeof chats.$inferSelect;
export type InsertChat = typeof chats.$inferInsert;

export type Media = typeof media.$inferSelect;
export type InsertMedia = typeof media.$inferInsert;

export type Document = typeof documents.$inferSelect;
export type InsertDocument = typeof documents.$inferInsert;

export type Keyword = typeof keywords.$inferSelect;
export type InsertKeyword = typeof keywords.$inferInsert;

export type ChatExport = typeof chatExports.$inferSelect;
export type InsertChatExport = typeof chatExports.$inferInsert;

export const schema = {
  users,
  chats,
  messages,
  media,
  documents,
  keywords,
  emergencyFlows,
  images,
  chatExports,
};
export const exampleSchema = {
  // Add your shared schema definitions here
  version: '1.0.0'
};

export default exampleSchema;

// Re-export existing schema from server/db/schema if it exists
export * from '../server/db/schema';
